import React, { useRef, useState } from "react";

import { useStorage } from "../../hooks/useStorage";
import axios from "axios";

interface Action {
  name: string;
  Action?: Function;
}
interface IProps {
  handle: Function;
}

const SaveTokenModel = ({ handle }: IProps) => {
  const storage = useStorage("localStorage");
  const [token, setToken] = useState("");
  const inputRef = useRef<HTMLInputElement>(null);

  const changeToken = (value: string) => {
    inputRef.current && (inputRef.current.value = value);
    setToken(value);
  };

  const saveToken = () => {
    storage.setItem("sm-ms-token", token);
    closeModel();
  };

  const closeModel = () => {
    handle("");
    changeToken("");
  };

  return (
    <div>
      <span>图床Token</span>
      <input
        type="text"
        ref={inputRef}
        onChange={() => changeToken(inputRef.current?.value!)}
      />
      <button onClick={saveToken}>确认</button>
      <button onClick={closeModel}>取消</button>
    </div>
  );
};

export default function UploaderAction() {
  const [activeAcition, setActiveAcition] = useState("");

  const handleSaveToken = () => {
    console.log("handleSaveToken");
    setActiveAcition("save-token");
  };

  const uploadImageFile = async (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    if (event.target.files && event.target.files.length > 0) {
      const formdata = new FormData();
      formdata.append("smfile", event.target.files[0]);
      const res = await axios.post(
        "http://114.67.246.0:8080/images/upload",
        formdata,
        { headers: { "Content-Type": "multipart/form-data" } }
      );
      console.log(res);
      console.log("上传成功，大概");
    }
  };

  const actions: Action[] = [
    { name: "上传" },
    { name: "设置", Action: handleSaveToken },
    { name: "删除" },
    { name: "搜索" },
  ];

  return (
    <>
      <div>
        {actions.map((item, index) => (
          <>
            {item.name === "上传" && (
              <input key={index} type="file" onChange={uploadImageFile} />
            )}
            <button key={index} onClick={() => item.Action && item.Action()}>
              {item.name}
            </button>
          </>
        ))}
      </div>
      <div>
        {activeAcition === "save-token" && (
          <SaveTokenModel
            handle={() => {
              setActiveAcition("");
            }}
          />
        )}
      </div>
    </>
  );
}
