import React, { useEffect, useRef, useState } from "react";
import UploaderHeader from "../../components/uploader-header/UploaderHeader";
import UploaderTable from "../../components/uploader-table/UploaderTable";
import UploaderAction from "../../components/uploader-action/UploaderAction";

import axios from "axios";

import "./index.css";
import { useStorage } from "../../hooks/useStorage";

export interface Image {
  created_at: string;
  delete: string;
  filename: string;
  hash: string;
  height: number;
  page: string;
  path: string;
  size: number;
  storename: string;
  url: string;
  width: number;
}

export default function ImageUploader() {
  const storage = useStorage("localStorage");
  const token = storage.getItem("sm-ms-token");

  const [images, setImages] = useState<Image[]>([]);
  const [count, setCount] = useState(0);
  const [pageIdx, setPageIdx] = useState(0);

  useEffect(() => {
    console.log("token", token);
    const fetchData = async () => {
      const res = await axios.get("http://114.67.246.0:8080/images/history", {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      setImages(res.data.data);
      setCount(res.data.setCount);
      setPageIdx(res.data.pageIdx);
      console.log(res);
    };
    fetchData();
  }, []);

  return (
    <div className={"uploader-ctn"}>
      <div style={{ zIndex: 90, position: "absolute" }}>
        <UploaderHeader />
        <div>
          <UploaderAction />
          <UploaderTable images={images} />
        </div>
      </div>
    </div>
  );
}
