export const useStorage = (
  defaultStorageType: "localStorage" | "sessionStorage"
) => {
  const defaultType = defaultStorageType;

  const getItem = (
    key: string,
    type: "localStorage" | "sessionStorage" = defaultType
  ) => {
    if (type === "localStorage") {
      return localStorage.getItem(key);
    } else if (type === "sessionStorage") {
      return sessionStorage.getItem(key);
    }
  };

  const setItem = (
    key: string,
    value: any,
    type: "localStorage" | "sessionStorage" = defaultType
  ) => {
    if (type === "localStorage") {
      return localStorage.setItem(key, value);
    } else if (type === "sessionStorage") {
      return sessionStorage.setItem(key, value);
    }
  };

  const removeItem = (
    key: string,
    type: "localStorage" | "sessionStorage" = defaultType
  ) => {
    if (type === "localStorage") {
      return localStorage.removeItem(key);
    } else if (type === "sessionStorage") {
      return sessionStorage.removeItem(key);
    }
  };
  const clear = () => {
    if (defaultStorageType === "localStorage") {
      return localStorage.clear();
    } else if (defaultStorageType === "sessionStorage") {
      return sessionStorage.clear();
    }
  };

  return { getItem, setItem, removeItem, clear };
};
